<template>
  <div id="content">
    <div class="content-bg">
      <!-- 头部滚动内容 -->
      <div class="content-item head">
        <div class="head-box">
          <div class="head-text">系统公告：暂无数据</div>
        </div>
      </div>
      <div class="content-item card">
        <div class="content-card-error" v-if="false">
          <img v-lazy="errorPage" alt="">
        </div>
        <div class="content-card-head">
          <!-- 最近文章轮播 数量：5-->
          <div class="card-head-carousel">
            <swiper :swiper-vo="postList[2]"></swiper>
          </div>
          <!-- 置顶文章 数量：1-->
          <div class="card-head-sticky">
            <!-- <card class="card-sticky" :card-vo="postList[2]"></card> -->
            <full-card class="card-sticky" :card-vo="postList[3]"></full-card>
          </div>
        </div>
        <div class="content-card-item">
          <div class="card-item-list">
            <card-list :row-show="listShow"></card-list>
          </div>
          <div class="card-item-page">
            Page 1 Of 7
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const Swiper = () => import('@/components/common/Swiper')
const FullCard = () => import('@/components/common/FullCard')
const CardList = () => import('@/components/home/CardList')
const Card = () => import('@/components/common/Card')
export default {
  components: {
    'card-list': CardList,
    'card': Card,
    'full-card': FullCard,
    'swiper': Swiper
  },
  props: {
    listShow: Object
  },
  data () {
    return {
      errorPage: 'static/error/15531615299599.jpg',
      loadStatus: true,
      rowShow: {
        row0: false,
        row1: false,
        row2: false,
        row3: false,
        row4: false},
      postList: [
        {image: '/static/image/sea.jpg', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '/static/img/computer.jpg', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '/static/loading/5-160914192R4-50.gif', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '/static/image/beach.jpg', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '/static/image/developer.jpg', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']},
        {image: '', title: '', desc: '', author: '', date: '', read: '', tag: ['1', '2']}
      ]
    }
  },
  mounted () {
    console.log('Content Loading ...')
  },
  beforeDestroy () {
    console.log('Content beforeDestroy 钩子执行...')
  },
  methods: {
  }
}
</script>

<style scoped>
#content{
  padding: 0px 0px 0px 0px;
  /* background-color: #cbeafb; */
}
.content-bg{
  /* border: 1px dashed blue; */
  /* background: linear-gradient(to bottom left, red, blue); */
  box-shadow: 0px -5px 3px rgba(18,18,19,0.5);
  padding: 0px 0px 0px 0px;
  /* background-color: #cbeafb; */
  border-radius: 30px;
}
/* .content-item{
  border: 1px dashed green;
} */
.head{
  color: #fff;
  height: 6vh;
  font-size: 13px;
  line-height: 6vh;
  /* border: 1px dashed blue; */
}
.head-box{
  margin: 0px 10px 0px 10px;
  /* border: 1px dashed blue; */
}
.card{
  /* background-color: rgba(244,248,251,1); */
  background-color: #fff;
  /* background: linear-gradient(to right bottom, #6d327c, #485DA6, #00a1ba, #01b18e, #32b37b); */
}
.content-card-head{
  border:1px dashed blue;
  padding: 15px 25px 15px 25px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  align-content: stretch;
}
.card-head-carousel{
  border:1px dashed green;
  padding: 10px 20px 10px 20px;
  width: 790px;
}
.card-head-sticky{
  border:1px dashed green;
  padding: 10px 20px 10px 20px;
}
.card-sticky{
  max-width: 375px;
  /* height: 420px; */
}
.content-card-item{
  border:1px dashed blue;
}
/* .card-item-list{
  display: none;
} */
.card-item-page{
  border:1px dashed green;
  text-align: center;
  padding: 50px 0px 50px 0px;
}
.content-card-error{
  border:1px dashed green;
  text-align: center;
  padding: 50px 0px 50px 0px;
  background-color: rgb(252, 184, 47);
}
.content-card-error img{
  height: 500px;
}
</style>



